window.names = station_names;
const stationNames = {};
station_names.split('@').map(item => {
    if (!item) return;
    const nams = item.split('|');
    const itemName = nams[3];
    const nameObj = {
        a: nams[0],
        stationName: nams[1],
        stationCode: nams[2],
        d: nams[3],
    }
    stationNames[itemName] = nameObj;
})
